.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro_img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.6;
    z-index: 1;  /* Ensure the mask overlay is above the image */
}

.hero {
    height: 100%;
    width: 100%;
    position: relative;  /* Make sure the hero position is relative for proper z-index stacking */
    z-index: 2;  /* Ensure the hero content is above the mask */
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
    z-index: 3;  /* Ensure the content is above everything else */
    color: white;  /* Ensure the content is visible on the dark mask */
}
.hero .content h1{
    font-size : 4rem;
    padding : 0.6rem 0 1.5rem;

}
.hero .content p{
    font-size : 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn{
    margin : 1rem 0.2rem;
}

@media screen and (max-width :640px){
    .hero .content h1{
        font-size : 4rem;
    
    }
    .hero .content p{
        font-size : 1.4rem;
    }
    
}